html, body {
    margin: 0;
    padding: 0;
    height: 100%;
  }
  
  .game-start-container {
    min-height: 100vh;
    min-width: 100vw;
    background: linear-gradient(to bottom, #93c5fd, #d8b4fe);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 0;
    position: fixed;
    top: 0;
    left: 0;
  }
  
  .game-start-content {
    text-align: center;
    max-width: 42rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding: 1rem;
  }
  
  .animated-shapes {
    display: flex;
    justify-content: center;
    gap: 2rem;
    margin-bottom: 2rem;
    animation: bounce 1s infinite;
  }
  
  .shape {
    transition: transform 0.3s ease;
  }
  
  .shape.pink {
    color: #ec4899;
  }
  
  .shape.blue {
    color: #3b82f6;
  }
  
  .shape.purple {
    color: #8b5cf6;
  }
  
  .game-title {
    font-size: 2.25rem;
    font-weight: bold;
    color: #6b21a8;
    margin-bottom: 1.5rem;
  }
  
  .start-button {
    background: linear-gradient(to right, #8b5cf6, #ec4899);
    color: white;
    font-size: 1.5rem;
    font-weight: bold;
    padding: 0.75rem 4rem;
    border-radius: 9999px;
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
  }
  
  .start-button:hover {
    transform: scale(1.05);
    box-shadow: 0 15px 20px -3px rgba(0, 0, 0, 0.2);
  }
  
  .start-button:active {
    transform: scale(0.95);
  }
  
  @keyframes bounce {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(-20px); }
  }